<template>
    <Head head-key="title" title="Fotoğraflarım" />

    <div id="kt_app_toolbar" class="app-toolbar py-3 py-lg-6">
        <div id="kt_app_toolbar_container" class="app-container container-fluid d-flex flex-stack">
            
            <div  class="page-title d-flex flex-column justify-content-center flex-wrap me-3">
                <h1 class="page-heading d-flex text-dark fw-bold fs-3 flex-column justify-content-center my-0 text-uppercase">
                    <span class="d-flex align-items-center">Fotoğraflarım</span>
                </h1>
            </div>

            <div class="d-flex align-items-center gap-2 gap-lg-3">
                <Link :href="route('photos.create',{language:$page.props.current_language})" class="btn fw-bold btn-sm btn-success d-flex align-items-center">
                    <i class="bi bi-plus-circle fs-4"></i>
                    <span class="ms-1 lh-1">YENİ</span>
                </Link>
            </div>

        </div>
    </div>
	
    <div id="kt_app_content" class="app-content  flex-column-fluid">
        <div id="kt_app_content_container" class="app-container container-fluid">
            
            <div class="card">

                <div class="card-header px-4">
                    <div class="card-toolbar w-100 justify-content-between gap-3">
                        <div class="d-flex flex-grow-1">
                            <Filter :filters="props.filters" :uri="route('photos.index')"/>
                        </div>
                    </div>
                </div>

                <div class="card-body p-0">

                    <DataTable scrollable :value="props.photos.data" stripedRows dataKey="id" tableStyle="min-width: 50rem">
                        <template #header>
                            <div>
                                <MultiSelect :modelValue="selectedColumns" :options="columns" optionLabel="header" @update:modelValue="onToggle" display="chip" placeholder="Select Columns" class="w-100"/>
                            </div>
                        </template>
                        <Column headerStyle="width: 2rem">
                            <template #body="{ data }">
                                <i class="bi bi-check-circle-fill text-success fs-2" v-if="data.approval_status === 1" v-tooltip.top="'Onaylanmış'"></i>
                                <i class="bi bi-x-circle-fill text-danger fs-2" v-if="data.approval_status === 2" v-tooltip.top="'İptal'"></i>
                                <i class="bi bi-pause-circle-fill text-warning fs-2" v-if="data.approval_status === 0" v-tooltip.top="'Yeni'"></i>
                            </template>
                        </Column>
                        <Column headerStyle="width: 3rem" header="Görsel">
                            <template #body="{ data }">
                                <a :href="data.main_image.original_url" :data-fancybox="`photo-${data.id}`"><img :src="data.main_image.preview_url" width="50" class="rounded"/></a>
                            </template>
                        </Column>
                        <Column field="id" header="ID" headerStyle="width: 4rem"></Column>
                        
                        <Column v-for="(col, index) of selectedColumns" :field="col.field" :header="col.header" :key="col.field + '_' + index" class="text-nowrap">
                            <template #body="{ data }">
                                <span v-if="col.field == 'created_at' || col.field == 'published_at' || col.field == 'shooting_date' || col.field == 'approval_date'">{{ formatDateToMask(data[col.field], true) }}</span>
                                <span v-if="col.field == 'published_by'">{{ data.publisher?.name }}</span>
                                <span v-if="col.field == 'approved_by'">{{ data.approver?.name }}</span>
                                <span v-if="col.field == 'photo_category'">{{ data.photo_type?.photo_family?.photo_sub_category?.photo_category?.name }}</span>
                                <span v-if="col.field == 'photo_sub_category'">{{ data.photo_type?.photo_family?.photo_sub_category?.name }}</span>
                                <span v-if="col.field == 'photo_family'">{{ data.photo_type?.photo_family?.name }}</span>
                                <span v-if="col.field == 'photo_type'">{{ data.photo_type?.photo_family?.name }}</span>
                                <span v-if="col.field == 'photos_count'">{{ data.gallery_images.length + 1 }}</span>
                                <span v-if="col.useField">{{ data[col.field] }}</span>
                            </template>
                        </Column>
                        
                        <Column class="text-end border-start" frozen alignFrozen="right" header="İşlemler">
                            <template #body="{ data }">
                                <div class="d-flex flex-nowrap justify-content-end">
                                    <Link :href="route('photos.edit',[data.id,{language:data.language}])" v-if="data.approval_status == 0">
                                        <Button icon="bi bi-pencil" text rounded severity="info" class="ms-2"/>
                                    </Link>
                                    <Link :href="route('my.photos.show',[data.id,{language:data.language}])">
                                        <Button icon="bi bi-eye" text rounded severity="info" class="ms-2"/>
                                    </Link>
                                </div>
                            </template>
                        </Column>
                    </DataTable>

                </div>
                <div class="card-footer">
                    <Pagination :links="props.photos.links" :only="['photos','flash']"/>
                </div>
            </div>

        </div>

    </div>

</template>

<script setup>

import {ref, onMounted} from "vue";

import Pagination from "../../Components/Pagination";
import DataTable from "primevue/datatable";
import Column from 'primevue/column';
import Button from "primevue/button";
import Filter from "../../Components/Filter";
import MultiSelect from "primevue/multiselect";

import { Fancybox } from "@fancyapps/ui";
import "@fancyapps/ui/dist/fancybox/fancybox.css";

const props = defineProps({
    photos:Object,
    filters:Object
});

const metaKey = ref(true);


const columns = ref([
    {field: 'name', header: 'Yerel Adı',useField:true},
    {field: 'photo_category', header: 'Kategori'},
    {field: 'photo_sub_category', header: 'Alt Kategori'},
    {field: 'photo_family', header: 'Aile'},
    {field: 'photo_type', header: 'Tür'},
    {field: 'photos_count', header: 'Fotoğraf Adeti'},
    {field: 'created_at', header: 'Oluşturulma Tarihi'},
    {field: 'shooting_date', header: 'Çekim Tarihi'},
    {field: 'approval_date', header: 'Onay Tarihi'},
    {field: 'approved_by', header: 'Onaylayan'},
    {field: 'published_at', header: 'Yayın Tarihi'},
    {field: 'published_by', header: 'Yayınlayan'},
]);
const selectedColumns = ref(columns.value);

const onToggle = (val) => {
    selectedColumns.value = columns.value.filter(col => val.includes(col));
};


const formatDateToMask = (date, withHour = false) => {
    
    if(!date) return '';

    var originalDate = new Date(date);
    var day = originalDate.getDate().toLocaleString().padStart(2, '0');
    var month = (originalDate.getMonth() + 1).toString().padStart(2, '0'); // Ay 0-11 arasında olduğu için +1 ekliyoruz.
    var year = originalDate.getFullYear();
    var hour = String(originalDate.getHours()).padStart(2, '0');
    var min = String(originalDate.getMinutes()).padStart(2, '0');
    var sec = String(originalDate.getSeconds()).padStart(2, '0');

    if( withHour ){
        return day + '.' + month + '.' + year + ' ' + hour + ':' + min + ':' + sec;
    }
    return day + '.' + month + '.' + year;
}

onMounted(() => {

    Fancybox.bind('[data-fancybox]');

});
</script>